@font-face {
  font-family: "icons";
  src: url("./icons.eot?21628910");
  src: url("./icons.eot?21628910#iefix") format("embedded-opentype"),
    url("./icons.woff2?21628910") format("woff2"),
    url("./icons.woff?21628910") format("woff"),
    url("./icons.ttf?21628910") format("truetype"),
    url("./icons.svg?21628910#icons") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
